@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;background-color:#282c34;color:white;font-family:"Roboto Slab",serif}.App-header{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:left;justify-content:left;font-size:1.8rem;padding:10px 0px 10px 25px;font-family:"Roboto Slab",serif;font-weight:bold}

.HomePage{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;padding-bottom:80px;background-color:#251d3b}.Welcome-Message{font-size:2.0rem;padding:30px 30px 30px 30px;font-weight:bold;padding-top:50px;padding-bottom:150px}.Qoute-Text{font-size:3.0rem;font-style:italic}.Qoute-Author{padding-top:15px;padding-left:900px;text-align:center;font-size:1.5rem}

.navBar{width:100%;color:white;background-color:#111;padding:10px}.navBar .navBar-navItem{font-size:24px;height:120px;display:inline;padding:15px}@media screen and (min-width: 768px){button{display:none}.links{height:auto;display:-webkit-flex;display:flex}li{padding:0 15px}}

.Projects-Header{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;font-size:2.0rem;font-weight:bold}

.AboutMe{padding-bottom:50px}.AboutMe-Header{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;font-size:2.0rem;font-weight:bold;padding-bottom:10px}.Profile-Picture{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:left;justify-content:left}.AboutMe-Content{padding-left:800px;max-width:450px}

.WorkHistory-Header{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;font-size:2.0rem;font-weight:bold;padding-bottom:20px}

.Navigation-bar{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;font-size:2.0rem;padding-bottom:100px;-webkit-justify-content:center;justify-content:center}.Navigation-bar{padding:0px 0px 10px 10px;font-weight:bold}

